import { Suspense } from 'react'
import { getPopularAnime } from '@/lib/api'
import { AnimeCard } from '@/components/anime-card'

// --- PERBAIKAN DISINI ---
// Ganti 'FunSkeletonGrid' menjadi 'SkeletonGrid'
import { SkeletonGrid } from '@/components/skeleton-loader' 

// Komponen Async untuk Fetch Data
async function PopularList() {
  const res = await getPopularAnime(1) // Fetch halaman 1
  const animes = res.data || []

  return (
    <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-3 sm:gap-4 md:gap-6">
      {animes.map((anime: any) => (
        <AnimeCard key={anime.mal_id} anime={anime} />
      ))}
    </div>
  )
}

export default function PopularPage() {
  return (
    <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 mt-16">
      <div className="mb-6">
        <h1 className="text-3xl font-bold text-foreground">Most Popular Anime</h1>
        <p className="text-muted-foreground mt-1">All time favorite animes sorted by popularity</p>
      </div>

      {/* --- PERBAIKAN DISINI --- */}
      {/* Gunakan SkeletonGrid */}
      <Suspense fallback={<SkeletonGrid count={25} />}>
        <PopularList />
      </Suspense>
    </main>
  )
}